/**
 * @classdesc Enum para identificar o target do LinkButton
 * 
 * @example
 * 
 * let linkButton:LinkButton = new LinkButton("Label LinkButton")
 *     .setTarget(ELinkTarget.BLANK);
 *   
 * let eLinkTarget:ELinkTarget = linkButton.getTarget();
 * 
 * @readonly
 * @enum {number}
 * @memberof module:mvcomponents/button
 */
export enum ELinkTarget{
	BLANK,PARENT,SELF,TOP
}
